import '../static/css/todayHeadline.css';
import uniSwipeAction from '@/components/common/uni-swipe-action';
export default {
	data() {
		return {
			nowTime: (new Date().getHours() < 10 ? '0' + new Date().getHours() : new Date().getHours()) + ':' + (new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes()),
			weekList: ['日','一','二','三','四','五','六'],
			ulList: [
				{
					time: '09:00',
					pointTime: '上午',
					interval: ['一','二','三','四','五']
				},{
					time: '18:00',
					pointTime: '下午',
					interval: ['一','二','三','四','五']
				},{
					time: '07:00',
					pointTime: '上午',
					interval: ['一','二','三','四','五']
				},{
					time: '13:14',
					pointTime: '下午',
					interval: ['日','六']
				}
			],
			opt: [{
				text: '删除',
				style: {
					backgroundColor: 'rgb(255,58,49)'
				}
			}],
		}
	},
	onLoad(options) {
		//动态设置标题
		if (options.label) {
		  uni.setNavigationBarTitle({
			title: options.label
		  })
		}
		uni.setNavigationBarColor({
		  frontColor: '#ffffff',
		  backgroundColor: '#208ffa',
		  animation: {
			duration: 400,
			timingFunc: 'easeIn'
		  }
		})
	},
	mounted(){
		
	},
	methods:{
		addAlarm(){
			//添加闹钟
			uni.navigateTo({
				url: '/views/pages/setAlarm',
			});
		},
		bindClick(value) {
			uni.showToast({
				title: `点击了${value.text}按钮`,
				icon: 'none'
			})
		},
	},
	components:{
		uniSwipeAction
	}
}
